.features__container__feature {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-direction: row;
    margin: 1rem;
}

.features__container__feature--title {
    flex: 1;
    max-width: 180px;
    margin-right: 2rem;
}

.features__container__feature--title h2 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 12px;
    line-height: 28px;
    letter-spacing: -0.04em;
    color: #fff;
}

.features__container__feature--title div {
    width: 38px;
    height: 3px;
    background: var(--gradient-bar);
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
    margin-bottom: 0.25rem;
}

.features__container__feature--text {
    flex: 2;
    max-width: 390px;
    display: flex;
}

.features__container__feature--text p {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 26px;
    color: var(--color-text);
}

@media screen and (max-width: 550px) {
    .features__container__feature--title h2 {
        font-size: 14px;
        line-height: 22px;
    }
    .features__container__feature--title p {
        font-size: 12px;
        line-height: 20px;
    }
    .features__container__feature {
        margin: 1rem, 0;
    }
}
